.bilibili-live-recorder {
  display: flex;
  position: fixed;
  z-index: 9999;
  top: 100px;
  right: 100px;
  width: 200px;
  height: 60px;
  border-radius: 3px;
  overflow: hidden;
  user-select: none;
  background: #fff;
  border: 1px solid #23ade5; }
  .bilibili-live-recorder *,
  .bilibili-live-recorder *::before,
  .bilibili-live-recorder *::after {
    box-sizing: border-box; }
  .bilibili-live-recorder .bilibili-live-recorder-states {
    display: flex;
    width: 60px;
    font-size: 16px;
    border-radius: 4px;
    color: #fff;
    border: 1px solid #fff; }
    .bilibili-live-recorder .bilibili-live-recorder-states .bilibili-live-recorder-state {
      display: none;
      justify-content: center;
      align-items: center;
      text-align: center;
      cursor: pointer;
      padding: 5px;
      letter-spacing: 2px;
      line-height: 1.4; }
      .bilibili-live-recorder .bilibili-live-recorder-states .bilibili-live-recorder-state.show {
        display: flex; }
      .bilibili-live-recorder .bilibili-live-recorder-states .bilibili-live-recorder-state.bilibili-live-recorder-state-before-record {
        background: #23ade5; }
  .bilibili-live-recorder .bilibili-live-recorder-monitor {
    display: flex;
    flex-direction: column;
    flex: 1;
    font-size: 13px;
    letter-spacing: 1px;
    color: #666;
    background: #fff; }
    .bilibili-live-recorder .bilibili-live-recorder-monitor .bilibili-live-recorder-monitor-top {
      display: flex;
      align-items: center;
      flex: 1;
      padding: 0 5px;
      line-height: 1;
      border-bottom: 1px solid #23ade5; }
    .bilibili-live-recorder .bilibili-live-recorder-monitor .bilibili-live-recorder-monitor-bottom {
      display: flex;
      align-items: center;
      flex: 1;
      padding: 0 5px;
      line-height: 1; }
  .bilibili-live-recorder .bilibili-live-recorder-handle {
    position: relative;
    width: 10px;
    cursor: move;
    border-radius: 4px;
    background: #23ade5;
    border: 1px solid #fff; }
    .bilibili-live-recorder .bilibili-live-recorder-handle::before {
      content: '';
      position: absolute;
      left: 3px;
      bottom: 4px;
      width: 3px;
      height: 3px;
      background: #c75744; }
    .bilibili-live-recorder .bilibili-live-recorder-handle::after {
      content: '';
      position: absolute;
      left: 3px;
      bottom: 10px;
      width: 3px;
      height: 3px;
      background: #c75744; }
